// App.js
import React, { useState } from 'react';
import SearchBar from './SearchBar';
import StockInfo from './StockInfo';
import StockGraph from './StockGraph'; // 새로운 컴포넌트 추가
import axios from 'axios';
import convert from 'xml-js';
import styled, { ThemeProvider } from 'styled-components';
import { createGlobalStyle } from 'styled-components';

// 다크모드와 라이트모드 테마 정의
const lightTheme = {
  background: '#f0f0f0',
  color: '#000',
  titleBg: '#0af07a',
  titleColor: '#000',
  buttonBg: '#228be6',
  buttonHoverBg: '#339af0',
  buttonActiveBg: '#1c7ed6',
};

const darkTheme = {
  background: '#333',
  color: '#fff',
  titleBg: '#0af07a',
  titleColor: '#fff',
  buttonBg: '#444',
  buttonHoverBg: '#555',
  buttonActiveBg: '#666',
};

const GlobalStyle = createGlobalStyle`
  body {
    background-color: ${({ theme }) => theme.background};
    color: ${({ theme }) => theme.color};
    transition: background-color 0.3s, color 0.3s;
  }
`;

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Title = styled.h1`
  background-color: ${({ theme }) => theme.titleBg};
  color: ${({ theme }) => theme.titleColor};
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
`;

const ToggleButton = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px;
  background-color: ${({ theme }) => theme.buttonBg};
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  &:hover {
    background-color: ${({ theme }) => theme.buttonHoverBg};
  }
  &:active {
    background-color: ${({ theme }) => theme.buttonActiveBg};
  }
`;

function App() {
  const [stockData, setStockData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [graphData, setGraphData] = useState([]); // 그래프 데이터를 위한 상태 추가
  const [isDarkMode, setIsDarkMode] = useState(false); // 다크모드 상태

  const fetchStockData = async (itmsNm, startDate, endDate) => {
    setLoading(true);
    setError(null);
    setStockData(null);
    setGraphData([]);

    const API_KEY = 'CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD+chVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ=='; 
    const API_URL = `https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo`;

    try {
      let date = new Date(startDate);
      const endDateObj = new Date(endDate);
      const stockDataList = [];

      while (date <= endDateObj) {
        const formattedDate = date.toISOString().split('T')[0].replace(/-/g, '');
        console.log(`요청 날짜: ${formattedDate}`); 

        const response = await axios.get(API_URL, {
          params: { 
            serviceKey: API_KEY,
            itmsNm: itmsNm,
            basDt: formattedDate
          }
        });

        console.log('API 응답:', response.data); 

        const jsonResult = convert.xml2js(response.data, { compact: true, spaces: 2 });
        console.log('변환된 JSON:', jsonResult); // 디버깅: JSON 변환 결과 확인

        const items = jsonResult.response.body.items?.item;

        if (items) {
          const itemArray = Array.isArray(items) ? items : [items];
          const stockItem = itemArray.find(item => item.itmsNm._text.toLowerCase() === itmsNm.toLowerCase());

          if (stockItem) {
            console.log(stockItem);
            stockDataList.push({
              date: formattedDate,
              itmsNm: stockItem.itmsNm?._text || '', // 주식 이름 문자열로 변환
            mrktCtg: stockItem.mrktCtg?._text || '', // 시장 분류 문자열로 변환
            clpr: parseFloat(stockItem.clpr?._text.replace(/,/g, '')) || 0, // 쉼표 제거 후 숫자로 변환
            fltRt: parseFloat(stockItem.fltRt?._text) || 0,
            basDt: formattedDate
            }
          );
          }
        }

        date.setDate(date.getDate() + 1); // 다음 날짜로 이동
      }

      if (stockDataList.length > 0) {
        console.log('수집된 주식 데이터:', stockDataList); // 디버깅: 수집된 주식 데이터 확인
        setGraphData(stockDataList); // 그래프 데이터 설정
        setStockData(stockDataList[stockDataList.length - 1]); // 마지막 날짜의 데이터 설정
      } else {
        console.log('해당 기간 동안의 데이터를 찾을 수 없습니다.'); // 디버깅: 데이터 없음 확인
        setGraphData([]);
        setStockData(null);
        setError('해당 기간 동안의 데이터를 찾을 수 없습니다.');
      }
    } catch (error) {
      console.error('API 호출 오류:', error); // 디버깅: API 호출 오류 확인
      setError('회사명을 정확히 입력해주세요.');
    } finally {
      setLoading(false);
    }
  };

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);
  };

  return (
    <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
      <GlobalStyle />
      <AppContainer>
        <Title>주식 시세 정보 조회</Title>
        <ToggleButton onClick={toggleTheme}>
          {isDarkMode ? '라이트 모드' : '다크 모드'}
        </ToggleButton>
        <SearchBar onSearch={fetchStockData} />
        {loading && <p>로딩 중...</p>}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        {stockData && <StockInfo data={stockData} />}
        {graphData.length > 0 && <StockGraph data={graphData} />} {/* 그래프 데이터가 있으면 그래프 표시 */}
      </AppContainer>
    </ThemeProvider>
  );
}

export default App;
