import React, { useState } from 'react';
import styled from 'styled-components';

const SearchForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const SearchInput = styled.input`
  border: none;
  background-color: #f1f1f1;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  margin-right: 10px;
  flex-grow: 1;
`;

const DateInput = styled.input`
  border: none;
  background-color: #f1f1f1;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 10px;
`;

const SearchButton = styled.button`
  border: none;
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;

  ${(props) => props.disabled && `
    background-color: #cccccc;
    cursor: not-allowed;
  `}
`;

const WeekdayDisplay = styled.p`
  color: #333;
  font-size: 14px;
  margin-top: 5px;
`;

const getWeekday = (dateString) => {
  const days = ['일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일'];
  const date = new Date(dateString);
  return days[date.getDay()];
};

function SearchBar({ onSearch }) {
  const [inputValue, setInputValue] = useState('SOOP');
  const [startDate, setStartDate] = useState('2024-06-03');
  const [endDate, setEndDate] = useState('2024-06-04');

  const handleSubmit = (event) => {
    event.preventDefault();
    if (inputValue.trim() === '') {
      alert('회사명을 입력해주세요.');
    } else {
      // 시작 날짜와 종료 날짜가 모두 입력되었는지 확인
      if (startDate && endDate) {
        onSearch(inputValue, startDate, endDate);
      } else {
        alert('시작 날짜와 종료 날짜를 모두 입력해주세요.');
      }
    }
  };

  const handleStartDateChange = (event) => {
    setStartDate(event.target.value);
  };

  const handleEndDateChange = (event) => {
    setEndDate(event.target.value);
  };

  return (
    <SearchForm onSubmit={handleSubmit}>
      <SearchInput
        type="text"
        value={inputValue}
        onChange={(e) => setInputValue(e.target.value)}
        placeholder="회사명을 입력해주세요."
      />
      <DateInput
        type="date"
        value={startDate}
        onChange={handleStartDateChange}
        placeholder="시작 날짜를 선택해주세요."
      />
      <DateInput
        type="date"
        value={endDate}
        onChange={handleEndDateChange}
        placeholder="종료 날짜를 선택해주세요."
      />
      <SearchButton type="submit">검색</SearchButton>
    </SearchForm>
  );
}


export default SearchBar;
